.NavBar
{
    position       : fixed;
    left           : 0;
    top            : 0;
    bottom         : 0;
    display        : flex;
    flex-direction : column;
    align-content  : center;
    justify-content: center;
}

@media (max-width: 600px)
{
    .NavBar
    {
        position      : fixed;
        flex-direction: row;
        bottom        : 0;
        left          : 0;
        right         : 0;
        top           : auto;
    }
}
