.Header-Container
{
    background-color: #282C34;
}

.Header-Logo
{
    min-height    : 80px;
    height        : 40%;
    pointer-events: none;
}

.Header
{
    background-color: inherit;
    min-height      : 100px;
    height          : 100vh;
    width           : 100%;
    display         : flex;
    flex-direction  : row;
    align-items     : center;
    justify-content : center;
    color           : #FFFFFF;
}

.Header-Text
{
    display       : flex;
    flex-direction: column;
    padding       : 0 20px;
}

.Header-Title
{
    
    color         : #B59959;
    font-family   : 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight   : bold;
    font-size     : 40px;
    text-transform: uppercase;
    margin        : 0;
}

.Header-Subtitle
{
    color         : #C0C0C0;
    font-family   : 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight   : lighter;
    font-size     : x-large;
    text-transform: uppercase;
    text-align    : left;
    margin        : 0;
}

@media (max-width: 500px)
{
    .Header
    {
        flex-direction: column;
    }

    .Header-Text
    {
        text-align: center;
    }
}

@media (prefers-reduced-motion: no-preference)
{
    .Blinking
    {
        animation: Cursor-Blink infinite 1s step-start;
    }
}

@keyframes Cursor-Blink
{
    0%
    {
        opacity: 0.0;
    }
    50%
    {
        opacity: 0.25;
    }
    100%
    {
        opacity: 0.0;
    }
}
